import React,{Component} from 'react';
import BookMainDetail from './BookMainDetail';
import BookComment from './BookComment';
import {
    BookMainWrapper,
    DetailTitle,
}from '../../style'
class BookMain extends  Component{
    constructor(props){
        super(props);
        this.state={
            showComment:false
        }
    }
   
    render(){
        const {book,comment}=this.props;
        return(
            <BookMainWrapper>
                <DetailTitle>
                    <span className="bookDeatil til">书本详情</span>
                    <span className="comment til ">评论</span>
                </DetailTitle>  
                
               <BookMainDetail book={book}/>   
               <BookComment comment={comment}/>
                
               
            </BookMainWrapper>
        )
    }
}
export default BookMain;